/* IC Theme: Polaroid */

.ic-theme--polaroid {
	--ic-image-background-color: #efefef;
	--ic-padding: 10px;

	--ic-width: 240px;
	--ic-height: 220px;// is the image height in this theme
	--ic-width-medium: var(--ic-width);
	--ic-height-medium: var(--ic-height);
	--ic-width-small: var(--ic-width);
	--ic-height-small: var(--ic-height);
}

.ic-theme--polaroid {

	.image-choices-choice .gform-field-label {
		display: flex !important;
		flex-direction: column;
		align-items: flex-start;
		width: 100%;
		border-radius: 3px;
		position: relative;
		padding: var(--ic-padding) var(--ic-padding) calc( var(--ic-padding) * 2 ) var(--ic-padding);
		background-color: #ffffff;
		box-shadow: 10px 20px 50px rgba(7, 30, 87, 0.2);
	}

	&.ic-cols--fixed .image-choices-choice .gform-field-label {
		width: var(--ic-width);
	}
	@include media-query(medium) {
		&.ic-cols-md--fixed .image-choices-choice .gform-field-label {
			width: var(--ic-width-medium);
		}
	}
	@include media-query(small) {
		&.ic-cols-sm--fixed .image-choices-choice .gform-field-label {
			width: var(--ic-width-small);
		}
	}

	.image-choices-choice-image-wrap,
	.image-choices-choice-image {
		border-radius: 3px;
	}
	.image-choices-choice-image-wrap {
		position: relative;
		display: block;
		width: 100%;
		height: var(--ic-height);
		margin-bottom: 30px;
		@include media-query(medium) {
			height: var(--ic-height-medium);
		}
		@include media-query(small) {
			height: var(--ic-height-small);
		}
	}
	&.gfield--type-product .image-choices-choice-image-wrap,
	&.gfield--type-option .image-choices-choice-image-wrap {
		margin-bottom: var(--ic-padding);
	}

	.image-choices-choice .gform-field-label:after {
		content: "";
		pointer-events: none;
		display: block;
		position: absolute;
		right: calc( var(--ic-padding) + 25px );
		top: calc(var(--ic-height) + var(--ic-padding));
		transform: translateY(-50%);
		width: 50px;
		height: 50px;
		overflow: hidden;
		border-radius: 50%;
		background-color: var(--ic-feature-color);
		background-image: var(--ic-selected-icon);
		background-position: 50%;
		background-size: var(--ic-selected-icon-size);
		background-repeat: no-repeat;
		z-index: 3;

		-webkit-transform-style: preserve-3d;
		backface-visibility: hidden;
		will-change: transform;
		transition: transform var(--ic-transition-duration) var(--ic-transition-easing), opacity var(--ic-transition-duration) var(--ic-transition-easing);
	}
	@include media-query(medium) {
		.image-choices-choice .gform-field-label:after {
			top: calc(var(--ic-height-medium) + var(--ic-padding));
		}
	}
	@include media-query(small) {
		.image-choices-choice .gform-field-label:after {
			top: calc(var(--ic-height-small) + var(--ic-padding));
		}
	}

	.image-choices-choice:not(.image-choices-choice-selected) .gform-field-label:after {
		transform: translateY(-50%) scale(0.8);
		opacity: 0;
	}


	.image-choices-choice-text,
	.image-choices-choice-price {
		display: block;
		position: relative;
		padding-left: var(--ic-padding);
		padding-right: var(--ic-padding);
		text-align: left;
		transition: all var(--ic-transition-duration) var(--ic-transition-easing);
	}

	.image-choices-choice-text + .image-choices-choice-price,
	.image-choices-choice-price + .image-choices-choice-text {
		padding-top: 5px;
	}
	// product field
	.image-choices-choice-text > .image-choices-choice-price {
		margin-bottom: 5px;
		padding-left: 0;
	}

	.image-choices-lightbox-btn {
		border-radius: 5px;
		top: calc( var(--ic-padding) * 2 );
		right: calc( var(--ic-padding) * 2 );
	}

	.image-choices-choice .gftt-icon {
		top: calc( var(--ic-height) + var(--ic-padding) * 2 );
		right: var(--ic-padding);
		@include media-query(medium) {
			top: calc( var(--ic-height-medium) + var(--ic-padding) * 2 );
		}
		@include media-query(small) {
			top: calc( var(--ic-height-small) + var(--ic-padding) * 2 );
		}
	}

	&.ic-image--natural {
		.image-choices-choice-image-wrap {
			height: auto !important;
			flex: 1;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}
		.image-choices-choice .gform-field-label:after {
			// when set to natural, the tick element is on the image wrap not the label
			content: none;
		}
		.image-choices-choice-image-wrap:after {
			content: "";
			pointer-events: none;
			display: block;
			position: absolute;
			right: 25px;
			bottom: 0;
			transform: translateY(50%);
			width: 50px;
			height: 50px;
			overflow: hidden;
			border-radius: 50%;
			background-color: var(--ic-feature-color);
			background-image: var(--ic-selected-icon);
			background-position: 50%;
			background-size: var(--ic-selected-icon-size);
			background-repeat: no-repeat;
			z-index: 3;

			-webkit-transform-style: preserve-3d;
			backface-visibility: hidden;
			will-change: transform;
			transition: transform var(--ic-transition-duration) var(--ic-transition-easing), opacity var(--ic-transition-duration) var(--ic-transition-easing);
		}
		.image-choices-choice:not(.image-choices-choice-selected) .image-choices-choice-image-wrap:after {
			transform: translateY(50%) scale(0.8);
			opacity: 0;
		}
		.image-choices-choice .gftt-icon {
			top: calc( var(--ic-padding) * 2 );
			right: auto;
			left: calc( var(--ic-padding) * 2 );
		}
	}

	&:not(.image-choices-show-labels) {
		.image-choices-choice-image-wrap {
			margin-bottom: 0;
		}
		.image-choices-choice .gform-field-label {
			padding-bottom: var(--ic-padding);
		}
	}


}
