body.hidden-scroll {
  overflow: hidden;
}

.jl-overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #fff;
  opacity: 0.7;
  display: none;
  z-index: 1035;
}

.jl-wrapper {
  z-index: 1040;
}
.jl-wrapper button {
  border: 0 none;
  background: transparent;
  font-size: 28px;
  padding: 0;
  cursor: pointer;
}
.jl-wrapper button:hover {
  opacity: 0.7;
}
.jl-wrapper .jl-close {
  display: none;
  position: fixed;
  right: 30px;
  top: 30px;
  z-index: 1060;
  margin-top: -14px;
  margin-right: -14px;
  height: 44px;
  width: 44px;
  line-height: 44px;
  font-family: Arial, Baskerville, monospace;
  color: #000;
  font-size: 3rem;
}
.jl-wrapper .jl-close:focus {
  outline: none;
}
.jl-wrapper .jl-counter {
  display: none;
  position: fixed;
  top: 30px;
  left: 30px;
  z-index: 1060;
  color: #000;
  font-size: 1rem;
}
.jl-wrapper .jl-navigation {
  width: 100%;
  display: none;
}
.jl-wrapper .jl-navigation button {
  position: fixed;
  top: 50%;
  margin-top: -22px;
  height: 44px;
  width: 22px;
  line-height: 44px;
  text-align: center;
  display: block;
  z-index: 1060;
  font-family: Arial, Baskerville, monospace;
  color: #000;
}
.jl-wrapper .jl-navigation button.jl-next {
  right: 5px;
  font-size: 2rem;
}
.jl-wrapper .jl-navigation button.jl-prev {
  left: 5px;
  font-size: 2rem;
}
.jl-wrapper .jl-navigation button:focus {
  outline: none;
}
@media (min-width: 35.5em) {
  .jl-wrapper .jl-navigation button {
    width: 44px;
  }
  .jl-wrapper .jl-navigation button.jl-next {
    right: 10px;
    font-size: 3rem;
  }
  .jl-wrapper .jl-navigation button.jl-prev {
    left: 10px;
    font-size: 3rem;
  }
}
@media (min-width: 50em) {
  .jl-wrapper .jl-navigation button {
    width: 44px;
  }
  .jl-wrapper .jl-navigation button.jl-next {
    right: 20px;
    font-size: 3rem;
  }
  .jl-wrapper .jl-navigation button.jl-prev {
    left: 20px;
    font-size: 3rem;
  }
}
.jl-wrapper .jl-image {
  position: fixed;
  touch-action: none;
  z-index: 10000;
}
.jl-wrapper .jl-image img {
  margin: 0;
  padding: 0;
  display: block;
  border: 0 none;
}
@media (min-width: 35.5em) {
  .jl-wrapper .jl-image img {
    border: 0 none;
  }
}
@media (min-width: 50em) {
  .jl-wrapper .jl-image img {
    border: 0 none;
  }
}
.jl-wrapper .jl-image iframe {
  background: #000;
  border: 0 none;
}
@media (min-width: 35.5em) {
  .jl-wrapper .jl-image iframe {
    border: 0 none;
  }
}
@media (min-width: 50em) {
  .jl-wrapper .jl-image iframe {
    border: 0 none;
  }
}
.jl-wrapper .jl-image .jl-caption {
  padding: 10px;
  color: #fff;
  background: rgba(0, 0, 0, 0.8);
  font-size: 1rem;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.jl-wrapper .jl-image .jl-caption.pos-top {
  bottom: auto;
  top: 0;
}
.jl-wrapper .jl-image .jl-caption.pos-outside {
