/* IC Theme: Simple */
.ic-theme--simple {
	--ic-image-background-color: #efefef;
	--ic-padding: 10px;

	--ic-width: 220px;
	--ic-height: 200px;
	--ic-width-medium: var(--ic-width);
	--ic-height-medium: var(--ic-height);
	--ic-width-small: var(--ic-width);
	--ic-height-small: var(--ic-height);
}

.ic-theme--simple {

	.image-choices-choice .gform-field-label {
		display: flex !important;
		flex-direction: column;
		align-items: center;
		justify-content: flex-end;
		width: 100%;
		max-width: 100%;
		position: relative;
	}

	&.ic-cols--fixed .image-choices-choice .gform-field-label {
		width: var(--ic-width);
	}
	@include media-query(medium) {
		&.ic-cols-md--fixed .image-choices-choice .gform-field-label {
			width: var(--ic-width-medium);
		}
	}
	@include media-query(small) {
		&.ic-cols-sm--fixed .image-choices-choice .gform-field-label {
			width: var(--ic-width-small);
		}
	}

	.image-choices-choice-image-wrap,
	.image-choices-choice-image-wrap:after {
		display: block;
		width: 100%;
		height: var(--ic-height);
		border-radius: 8px;
	}
	@include media-query(medium) {
		.image-choices-choice-image-wrap,
		.image-choices-choice-image-wrap:after {
			height: var(--ic-height-medium);
		}
	}
	@include media-query(small) {
		.image-choices-choice-image-wrap,
		.image-choices-choice-image-wrap:after {
			height: var(--ic-height-small);
		}
	}

	.image-choices-choice-image-wrap {
		position: relative;
		z-index: 1;
		margin-bottom: var(--ic-padding);
		img {
			border-radius: 8px;
			position: relative;
			z-index: 0;
		}
	}
	.image-choices-choice-image-wrap:after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		pointer-events: none;
		box-shadow: inset 0 0 0 0 var(--ic-feature-color);
		will-change: box-shadow;
		transition: box-shadow var(--ic-transition-duration) var(--ic-transition-easing);
		z-index: 1;
	}
	.image-choices-choice-selected .image-choices-choice-image-wrap:after {
		box-shadow: inset 0 0 0 6px var(--ic-feature-color);
	}


	.image-choices-choice-text,
	.image-choices-choice-price {
		display: block;
		position: relative;
		text-align: center;
		transition: all var(--ic-transition-duration) var(--ic-transition-easing);
	}

	.image-choices-choice-text + .image-choices-choice-price,
	.image-choices-choice-price + .image-choices-choice-text {
		padding-top: 5px;
	}
	// product field
	.image-choices-choice-text > .image-choices-choice-price {
		margin-bottom: 5px;
	}

	.image-choices-lightbox-btn {
		border-radius: 5px;
		top: var(--ic-padding);
		right: var(--ic-padding);
	}

	.image-choices-choice .gftt-icon {
		top: var(--ic-padding);
		right: unset;
		left: var(--ic-padding);
	}

	&.ic-image--natural {
		.image-choices-choice-image-wrap {
			height: auto;
		}
		.image-choices-choice-image-wrap:after {
			height: 100%;
		}
	}

}
