/* IC Theme: Cover Tile */

.ic-theme--cover-tile {
	--ic-image-background-color: #efefef;
	--ic-padding: 25px;

	--ic-width: 240px;
	--ic-height: 320px;
	--ic-width-medium: var(--ic-width);
	--ic-height-medium: var(--ic-height);
	--ic-width-small: var(--ic-width);
	--ic-height-small: var(--ic-height);

	--ic-text-weight: bold;
	--ic-text-color: #ffffff;
	--ic-selected-text-color: var(--ic-text-color);
	--ic-selected-text-weight: var(--ic-text-weight);

	--ic-price-color: var(--ic-text-color);
	--ic-price-weight: normal;
	--ic-selected-price-color: var(--ic-text-color);
}

.ic-theme--cover-tile {

	.image-choices-choice .gform-field-label,
	.image-choices-choice .gform-field-label:after,
	.image-choices-choice-image-wrap,
	.image-choices-choice-image {
		border-radius: 5px;
	}


	.image-choices-choice:after {
		content: "" !important;
		pointer-events: none;
		display: block;
		position: absolute;
		left: 50%;
		bottom: 0;
		transform: translate(-50%, 50%);
		width: 50px;
		height: 50px;
		overflow: hidden;
		border-radius: 50%;
		background-color: var(--ic-feature-color);
		background-image: var(--ic-selected-icon);
		background-position: 50%;
		background-size: var(--ic-selected-icon-size);
		background-repeat: no-repeat;
		z-index: 3;

		-webkit-transform-style: preserve-3d;
		backface-visibility: hidden;
		will-change: transform;
		transition: transform var(--ic-transition-duration) var(--ic-transition-easing), opacity var(--ic-transition-duration) var(--ic-transition-easing);
		transition-delay: var(--ic-transition-duration);
	}

	.image-choices-choice:not(.image-choices-choice-selected):after {
		transition-delay: 0s;
		transform: translate(-50%, 50%) scale(0.5);
		opacity: 0;
	}

	.image-choices-choice .gform-field-label {
		width: 100%;
		height: var(--ic-height);
		position: relative;
		display: flex !important;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-end;
		padding: var(--ic-padding);
		@include media-query(medium) {
			height: var(--ic-height-medium);
		}
		@include media-query(small) {
			height: var(--ic-height-small);
		}
	}

	&.ic-cols--fixed .image-choices-choice .gform-field-label {
		width: var(--ic-width);
	}
	@include media-query(medium) {
		&.ic-cols-md--fixed .image-choices-choice .gform-field-label {
			width: var(--ic-width-medium);
		}
	}
	@include media-query(small) {
		&.ic-cols-sm--fixed .image-choices-choice .gform-field-label {
			width: var(--ic-width-small);
		}
	}

	.image-choices-choice .gform-field-label:after {
		content: "";
		pointer-events: none;
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		background: linear-gradient(180deg, rgba(0, 0, 0, 0) 3.8%, rgba(0, 0, 0, 0.51) 88.61%);
		z-index: 2;
	}

	.image-choices-choice-image-wrap {
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		box-shadow: 10px 20px 50px rgba(7, 30, 87, 0.2);
		z-index: 1;
	}

	.image-choices-choice-text,
	.image-choices-choice-price {
		display: block;
		position: relative;
		width: 100%;
		z-index: 3;
		transition: all var(--ic-transition-duration) var(--ic-transition-easing);
	}
	&.gfield--type-option .image-choices-choice-text,
	&.gfield--type-option .image-choices-choice-price {
		transition: none;
	}


	.image-choices-choice-selected .image-choices-choice-text,
	&:not(.gfield--type-product) .image-choices-choice-selected .image-choices-choice-price {
		transform: translateY(-20px);
	}

	.image-choices-lightbox-btn {
		border-radius: 5px;
		top: calc( var(--ic-padding) / 2 );
		right: calc( var(--ic-padding) / 2 );
	}

	.image-choices-choice .gftt-icon {
		top: calc( var(--ic-padding) / 2 );
		right: unset;
		left: calc( var(--ic-padding) / 2 );
	}

	&.ic-image--natural {
		.image-choices-choice .gform-field-label {
			height: auto;
		}
	}

	&.ic-image--contain {
		.image-choices-choice-image-wrap {
			background-color: #000;
		}
	}

}
