@mixin template-columns( $name: "", $cols: 1, $size: "" ) {
	$width: 100%;
	@if ( $cols == 2 ) {
		$width: 50%;
	}
	@else if ( $cols == 3 ) {
		$width: 33.333%;
	}
	@else if ( $cols == 4 ) {
		$width: 25%;
	}
	@else if ( $cols == 5 ) {
		$width: 20%;
	}
	@else if ( $cols == 6 ) {
		$width: 16.666%;
	}
	@else if ( $cols == 7 ) {
		$width: 14.285%;
	}
	@else if ( $cols == 8 ) {
		$width: 12.5%;
	}
	@else if ( $cols == 9 ) {
		$width: 11.111%;
	}
	@else if ( $cols == 10 ) {
		$width: 10%;
	}
	@else if ( $cols == 11 ) {
		$width: 9.09%;
	}
	@else if ( $cols == 12 ) {
		$width: 8.333%;
	}

	.ic-cols#{$size}--#{$name} .image-choices-choice {
		//width: calc(#{$width} - var(--ic-column-spacing) / 2);
		width: calc(#{$width} - var(--ic-column-spacing));
	}

}

@media only screen and (min-width:1080px) {

	// on desktop/large screens, the auto columns uses grid to auto-fit all columns on one row
	.gfield.image-choices-field,
	.gform_legacy_markup_wrapper .gfield.image-choices-field,
	.gform_wrapper.gravity-theme .gfield--type-choice.image-choices-field,
	.gform_wrapper.gform-theme--framework .gfield--type-choice.image-choices-field {
		&.ic-cols--auto {
			.gfield_checkbox,
			.gfield_radio {
				display: grid;
				grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
			}
		}
	}

	@include template-columns("12", 12);
	@include template-columns("11", 11);
	@include template-columns("10", 10);
	@include template-columns("9", 9);
	@include template-columns("8", 8);
	@include template-columns("7", 7);
	@include template-columns("6", 6);
	@include template-columns("5", 5);
	@include template-columns("4", 4);
	@include template-columns("3", 3);
	@include template-columns("2", 2);
	@include template-columns("1", 1);

}

@media only screen and (min-width:640px) and (max-width:1079px) {

	// on tablet/medium screens, the auto columns is fixed to 4
	@include template-columns("auto", 3, "-md");

	@include template-columns("12", 12, "-md");
	@include template-columns("11", 11, "-md");
	@include template-columns("10", 10, "-md");
	@include template-columns("9", 9, "-md");
	@include template-columns("8", 8, "-md");
	@include template-columns("7", 7, "-md");
	@include template-columns("6", 6, "-md");
	@include template-columns("5", 5, "-md");
	@include template-columns("4", 4, "-md");
	@include template-columns("3", 3, "-md");
	@include template-columns("2", 2, "-md");
	@include template-columns("1", 1, "-md");

}

@media only screen and (max-width:639px) {

	// on mobile/small screens, the auto columns is fixed to 2
	@include template-columns("auto", 1, "-sm");

	@include template-columns("12", 12, "-sm");
	@include template-columns("11", 11, "-sm");
	@include template-columns("10", 10, "-sm");
	@include template-columns("9", 9, "-sm");
	@include template-columns("8", 8, "-sm");
	@include template-columns("7", 7, "-sm");
	@include template-columns("6", 6, "-sm");
	@include template-columns("5", 5, "-sm");
	@include template-columns("4", 4, "-sm");
	@include template-columns("3", 3, "-sm");
	@include template-columns("2", 2, "-sm");
	@include template-columns("1", 1, "-sm");

}
